import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Loading, AlertController, ToastController, LoadingController } from 'ionic-angular';

import { HomePage } from '../home/home';
/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  loading: Loading;
  // registerCredentials = {username: '', password: ''};
    
    username : string;
    password : string;
  constructor(public navCtrl: NavController, public navParams: NavParams,  public loadingCtrl: LoadingController, public alertCtrl: AlertController, public toastCtrl: ToastController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  login(){
      this.showLoading();


      if (this.username != 'admin' || this.password != 'admin' ) {
          this.loading.dismiss();
          this.showAlert("Mohon cek kembali username dan password anda.");
      }
      else{
        this.navCtrl.setRoot(HomePage);
        this.loading.dismiss();
        this.loginSuccessToast();
      }
      
    }

    passwordType: string = 'password';
    passwordIcon: string = 'eye-off';

    hideShowPassword() {
     this.passwordType = this.passwordType === 'text' ? 'password' : 'text';
     this.passwordIcon = this.passwordIcon === 'eye-off' ? 'eye' : 'eye-off';
    }

    showLoading(){
      this.loading = this.loadingCtrl.create({
        content: 'Logging in...'
      });
      this.loading.present();
    }

    showAlert(text) {
      let alert = this.alertCtrl.create({
        title: 'Gagal Masuk!',
        subTitle: text,
        buttons: ['Try again']
      });
      alert.present();
    }

    loginSuccessToast() {
      let toast = this.toastCtrl.create({
        message: 'Berhasil Masuk',
        duration: 2000
      });
      toast.present();
    }

}



